.container{
    max-width: 950px;
    margin: 0 auto;
    padding: 0 10px;
}
.title {
    font-weight: 700;
    font-size: 34px;
    text-align: center;
    margin-bottom: 40px;
}
.sr-only {
    position: absolute;
    width: 1px;
    height: 1px;
    padding: 0;
    margin: -1px;
    overflow: hidden;
    clip: rect(0,0,0,0);
    border: 0;
}