.projectBlock {
  padding-bottom: 5px;
  min-height: 150px;
  box-shadow: 0 2px 10px 0 #070707;
  border-radius: 12px;
  overflow: hidden;
  transition: all 0.3s;
  margin-bottom: 30px;

  &:hover {
    transform: scale(1.05) translateX(5px) translateY(-5px);
    box-shadow: 0 4px 8px rgba(97, 97, 97, 0.58), 0px 8px 20px rgba(97, 97, 97, 0.58);
    filter: saturate(120%) brightness(105%);

    & .stackInfo {
      filter: saturate(100%) brightness(100%);
      opacity: 1;
    }
  }
}

.imgBlock {
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 20px;
  align-items: center;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}

.stackInfo {
  width: 180px;
  font-weight: 500;
  line-height: 1.4;
  padding: 5px;
  background-color: rgb(245, 245, 245);
  border-radius: 12px;
  border: 1px solid #E0E0E0;
  -webkit-transition: opacity 0.3s;
  -moz-transition: opacity 0.3s;
  -ms-transition: opacity 0.3s;
  -o-transition: opacity 0.3s;
  transition: opacity 0.3s;
  box-shadow: 0 2px 10px 0 #070707;
  opacity: 0;
}

.stack {
  margin-bottom: 5px;

  span {
    display: block;
    font-size: 19px;
  }
}

.linksBlock {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
}

.link {
  position: relative;
  padding: 5px 15px 4px;
  margin: 0 auto;
  text-align: center;
  cursor: pointer;
  border-radius: 10px;
  border: 1px solid #070707;
  background-color: rgba(255, 255, 255, 0.9);
  color: #070707;
  -webkit-transition: border-color 0.3s, box-shadow 0.3s;
  -moz-transition: border-color 0.3s, box-shadow 0.3s;
  -ms-transition: border-color 0.3s, box-shadow 0.3s;
  -o-transition: border-color 0.3s, box-shadow 0.3s;
  transition: border-color 0.3s, box-shadow 0.3s;

  &:hover {
    border-color: black;
    box-shadow: 0 2px 4px rgba(97, 97, 97, 0.3), 0px 4px 8px rgba(97, 97, 97, 0.3);
  }

  &:active {
    box-shadow: inset 0px 10px 4px rgba(0, 0, 0, 0.1);
  }
}

.info {
  padding: 5px 5px 5px 10px;
  height: 30%;
}

.title {
  font-size: 18px;
    position: relative;
  letter-spacing: 0.005em;
  margin-bottom: 5px;

  &::after {
    position: absolute;
    content: '';
    left: 0px;
    bottom: -2px;
    width: 20px;
    height: 1px;
    background-color: black;
  }
}

.descr {
  color: #414141;
  font-size: 16px;
  text-align: left;
}


.stackInfo{
  padding: 10px;
  width: 85%;
}
.stackBlock{
display: flex;
width: 100%;
gap: 10px;
flex-wrap: wrap;
justify-content: center;
align-items: center;
margin-bottom: 20px;
}
.stackImg{
max-width: 30px;
max-height: 30px;
}

@media(max-width: 1300px) {
  .projectBlock {
    &:hover {
      transform: scale(1.05) translateX(3px) translateY(-3px);
      box-shadow: 0 2px 4px rgba(97, 97, 97, 0.58), 0 4px 10px rgba(97, 97, 97, 0.58);
    }
  }
}

